@import "../../sass/mixins"

.drink

  &__name
    font-size: 48px
    line-height: 1.5
    margin-bottom: 25px

    @include md
      font-size: 2.5em
      margin-bottom: 0.5em

  &__description
    max-width: 50%
    margin-bottom: 40px
    line-height: 1.6

    @include md
      max-width: 90%
      font-size: 1em

  &__subheading
    font-size: 24px
    line-height: 1.5

  &__ingredients
    padding: 0
    padding-left: 2rem
    margin: 0.5rem 0 30px
    text-transform: capitalize
    display: flex
    flex-direction: column

    &-item
      margin: 5px 0

  &__extra
    padding: 0
    padding-left: 2rem
    margin: 0.5rem 0 30px
    display: flex
    flex-direction: column

    &-item
      margin: 5px 0
